.pokemon__card {
  width: 200px;
  height: 300px;
  margin: 5px;
  background-color: aliceblue;
  border: 1px solid black;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.pokemon__image {
  width: 180px;
  height: 400px;
  position: relative;
  top: 20px;
}

.pokemon__image:hover {
  transform: rotate(45deg);
}

.pokemon__name {
  width: 180px;
  padding: 10px;
  font-size: 25px;
  font-weight: 700;
  text-align: center;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  text-transform: capitalize;
  position: relative;
  top: 20px;
}

.pokemon__types {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  margin: 0;
  padding: 0;
}

.type {
  font-size: 12px;
  margin: 10px;
  background-color: grey;
  color: white;
  text-transform: uppercase;
  padding: 5px;
  border-radius: 10px;
  cursor: pointer;
}

.type:hover {
  background-color: lightgrey;
  color: black;
}
